.h1 {
  position: relative;
  font-weight: 900;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.h1,
.h1-wrapper,
.h2 {
  align-self: stretch;
}
.h1-wrapper {
  height: 45px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.h2 {
  margin: 0;
  position: relative;
  font-size: 16px;
  font-weight: 400;
  font-family: "Montserrat Alternates";
  color: #aeaeae;
}
.frame-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1px;
}
.c {
  position: relative;
  font-size: 14px;
  font-weight: 900;
  font-family: Montserrat;
  color: #fff;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.c-wrapper {
  cursor: pointer;
  border: 0;
  padding: 15px 95px;
  background-color: #000;
  width: 366px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 18px;
  height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.gem-stone-1-1-icon {
  width: 120px;
  position: relative;
  height: 120px;
}
.div {
  position: relative;
  font-size: 24px;
  font-weight: 900;
  font-family: Montserrat;
  color: #fff;
  text-align: left;
  text-shadow: 0.3px 0 0#000, 0 0.3px 0#000, -0.3px 0 0#000, 0-0.3px 0#000;
}
.button-premium {
  cursor: pointer;
  border: 1px solid #000;
  padding: 0 144px;
  background-color: #52d4ff;
  width: 366px;
  border-radius: 20px;
  box-sizing: border-box;
  height: 189px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.speech-balloon-1 {
  align-self: stretch;
  flex: 1;
  position: relative;
}
.button-support {
  cursor: pointer;
  border: 1px solid #000;
  padding: 30px 121px 23px;
  background-color: #ffa2a2;
  width: 366px;
  border-radius: 20px;
  box-sizing: border-box;
  height: 189px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main1 {
  align-self: stretch;
  flex-direction: column;
  gap: 27px;
}
.indexhtml,
.main,
.main1 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main {
  height: 635px;
  flex-direction: column;
  gap: 30px;
}
.indexhtml {
  width: 100%;
  position: relative;
  background-color: #fff;
  height: 844px;
  overflow: hidden;
  flex-direction: row;
  padding: 61px 12px;
  box-sizing: border-box;
  text-align: left;
  font-size: 32px;
  color: #000;
  font-family: Montserrat;
}
