.gif-icon,
.h11 {
  position: relative;
}
.h11 {
  align-self: stretch;
  font-weight: 900;
}
.gif-icon {
  width: 168px;
  height: 174px;
  margin-top: -22px;
}
.h1-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.div2 {
  position: relative;
  font-size: 16px;
  font-weight: 900;
  font-family: Montserrat;
  color: #fff;
  text-align: left;
}
.button-index-html,
.frame-group,
.th-page {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.button-index-html {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: #000;
  width: 360px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 18px;
  height: 60px;
  justify-content: center;
}
.frame-group,
.th-page {
  justify-content: flex-start;
}
.frame-group {
  align-self: stretch;
  gap: 21px;
}
.th-page {
  width: 100%;
  position: relative;
  background-color: #fff;
  height: 844px;
  overflow: hidden;
  padding: 180px 0;
  box-sizing: border-box;
  text-align: center;
  font-size: 20px;
  color: #000;
  font-family: Montserrat;
}
