.div22,
.div23 {
  position: relative;
  font-weight: 900;
}
.div22 {
  width: 295px;
  display: inline-block;
}
.div23 {
  font-size: 32px;
  font-family: Montserrat;
  color: #000;
  text-align: center;
}
.wrapper {
  cursor: pointer;
  border: 1px solid #000;
  padding: 24px 69px;
  background-color: #fff;
  align-self: stretch;
  border-radius: 20px;
  box-sizing: border-box;
  height: 96px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.div24 {
  position: relative;
  font-size: 32px;
  font-weight: 900;
  font-family: Montserrat;
  color: #fff;
  text-align: center;
}
.frame-button {
  cursor: pointer;
  border: 1px solid #000;
  padding: 24px 69px;
  background-color: #000;
  align-self: stretch;
  border-radius: 20px;
  box-sizing: border-box;
  height: 96px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.div25 {
  position: relative;
  font-size: 25px;
  font-weight: 900;
  font-family: Montserrat;
  background: linear-gradient(90deg, #cf7eec, #ffa2a2 53%, #52d4ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
}
.frame-parent4,
.wrapper1 {
  align-self: stretch;
  display: flex;
}
.wrapper1 {
  cursor: pointer;
  border: 1px solid #000;
  padding: 33px 43px;
  background-color: #000;
  border-radius: 20px;
  box-sizing: border-box;
  height: 96px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.frame-parent4 {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 17px;
}
.parent2,
.th-page1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.parent2 {
  width: 309px;
  justify-content: flex-start;
  gap: 17px;
}
.th-page1 {
  width: 100%;
  position: relative;
  background-color: #fff;
  height: 844px;
  overflow: hidden;
  justify-content: center;
  padding: 65px 0 254px;
  box-sizing: border-box;
  text-align: center;
  font-size: 32px;
  color: #000;
  font-family: Montserrat;
}
