/* Premium.css */
.button-back-child {
  width: 34px;
  position: relative;
  max-height: 100%;
  object-fit: contain;
}
.button-back {
  cursor: pointer;
  border: 1px solid #000;
  padding: 16px 14px;
  background-color: #fff;
  width: 62px;
  border-radius: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.div10,
.div8 {
  position: relative;
  font-weight: 900;
}
.div8 {
  align-self: stretch;
}
.div10 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.button-month,
.button-week {
  width: 100px;
  border-radius: 10px;
  border: 1px solid #000;
  box-sizing: border-box;
  height: 28px;
  padding: 6px 12px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.button-week.selected,
.button-month.selected,
.button-year.selected {
  background-color: #ffcbcb;
}
.button-month {
  background-color: rgba(82, 212, 255, 0.5);
}
.button-month,
.button-year,
.div9 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.button-year {
  width: 100px;
  border-radius: 10px;
  background-color: rgba(255, 162, 162, 0.7);
  border: 1px solid #000;
  box-sizing: border-box;
  height: 28px;
  padding: 6px 12px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.div9 {
  align-self: stretch;
  gap: 7px;
  font-size: 12px;
}
.frame-div,
.frame-parent1 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.frame-div {
  align-items: center;
  padding: 0 13px;
  gap: 11px;
}
.frame-parent1 {
  align-items: flex-start;
  gap: 15px;
}
.div16 {
  align-self: stretch;
  position: relative;
  font-size: 15px;
  font-weight: 900;
  color: #000;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
}
.frame2 {
  width: 301px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 31px;
  z-index: 0;
}
.rectangle-input {
  margin: 0 !important;
  width: 35px;
  position: absolute;
  top: 19px;
  left: 283px;
  border-radius: 10px;
  border: 1px solid #000;
  box-sizing: border-box;
  height: 35px;
  z-index: 1;
}
.button-free,
.eurweek,
.plus {
  align-self: stretch;
  position: relative;
}
.button-free {
  border-radius: 15px;
  background-color: #fff;
  border: 1px solid #000;
  box-sizing: border-box;
  height: 160px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 19px 18px;
  gap: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.button-free.selected {
  transform: scale(1.05);
  border-color: #007bff;
}
.eurweek,
.plus {
  font-weight: 900;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.eurweek {
  font-size: 15px;
  color: #000;
  margin-top: -2px;
}
.plus-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.div17 {
  width: 296px;
  position: relative;
  font-weight: 900;
  display: flex;
  align-items: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.button-free-parent,
.frame-container {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 23px;
}
.button-free-parent {
  align-items: flex-start;
  text-align: left;
  font-size: 12px;
  color: #676767;
}
.frame-container {
  align-items: center;
  padding: 0 3.5px;
}
.div21 {
  position: relative;
  font-weight: 900;
}
.button-subscribe {
  align-self: stretch;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
  background-color: #000;
  border: 1px solid #000;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 20px 85px;
  cursor: pointer;
  font-size: 14px;
  color: #fff;
}
.button-back-parent,
.frame1,
.premium {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.frame1 {
  align-self: stretch;
  align-items: center;
  gap: 33px;
}
.button-back-parent,
.premium {
  width: 347px;
  align-items: flex-start;
}
.premium {
  width: 100%;
  position: relative;
  background-color: #fff;
  height: 844px;
  overflow: hidden;
  align-items: center;
  padding: 32px 0;
  box-sizing: border-box;
  text-align: center;
  font-size: 20px;
  color: #000;
  font-family: Montserrat;
}
