.h12 {
  width: 295px;
  position: relative;
  font-size: 32px;
  font-weight: 900;
  color: #000;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-generate-child {
  width: 350px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  background-color: #000;
  border: 0.3px solid #000;
  box-sizing: border-box;
  height: 170px;
  z-index: 0;
}
.capa-1-1-icon {
  width: 70px;
  position: relative;
  height: 70px;
  overflow: hidden;
  flex-shrink: 0;
}
.capa-1-1-wrapper {
  width: 155px;
  border-radius: 20px;
  background-color: #fff;
  border-top: 0.4px solid #000;
  border-bottom: 0.4px solid #000;
  border-left: 0.4px solid #000;
  box-sizing: border-box;
  height: 170px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 60px 52px;
  z-index: 1;
}
.div3,
.div4 {
  align-self: stretch;
  position: relative;
  text-align: left;
}
.div3 {
  font-size: 15px;
  font-weight: 900;
  font-family: Montserrat;
  color: #fff;
}
.div4 {
  font-size: 13px;
  line-height: 100%;
  font-family: Inter;
  color: #969696;
}
.button-generate,
.parent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.parent {
  flex: 1;
  flex-direction: column;
  padding: 27px 0 0;
  gap: 10px;
  z-index: 2;
}
.button-generate {
  cursor: pointer;
  border: 0;
  padding: 0 0 5px;
  background-color: transparent;
  align-self: stretch;
  flex-direction: row;
  gap: 12px;
}
.button-generate,
.div5,
.div6 {
  position: relative;
}
.div5 {
  align-self: stretch;
  font-weight: 900;
}
.div6 {
  width: 174px;
  font-size: 13px;
  line-height: 100%;
  font-family: Inter;
  color: #969696;
  display: flex;
  align-items: center;
}
.button-paste,
.web {
  align-self: stretch;
  position: relative;
}
.button-paste {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 5px;
  gap: 12px;
  cursor: pointer;
}
.web {
  font-size: 13px;
  line-height: 100%;
  font-family: Inter;
  color: #969696;
}
.h1-group,
.nd-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.h1-group {
  width: 350px;
  gap: 11px;
}
.nd-page {
  width: 100%;
  position: relative;
  background-color: #fff;
  height: 844px;
  padding: 84px 0;
  box-sizing: border-box;
  text-align: left;
  font-size: 15px;
  color: #fff;
  font-family: Montserrat;
}
